<template>
  <div>
    <my-header></my-header>
    <div class="bioinfo-db-overview">
      <b-container fluid>
        <b-row class="justify-content-md-center">
          <b-col cols="12" md="10">
            <h1 class="text-center mt-5">生信数据库Overview</h1>
            <b-card class="my-5">
              <b-row>
                <b-col md="6">
                  <b-card-title>数据库简介</b-card-title>
                  <b-card-text>
                    生信数据库是一个集成了各种生物信息学数据资源的平台，包括基因序列、蛋白质结构、功能注释、表达谱数据等，帮助科研人员更好地理解生物过程和研究复杂疾病。
                  </b-card-text>
                </b-col>
                <b-col md="6">
                  <b-card-title>数据来源</b-card-title>
                  <b-list-group>
                    <b-list-group-item>NCBI</b-list-group-item>
                    <b-list-group-item>UniProt</b-list-group-item>
                    <b-list-group-item>PDB</b-list-group-item>
                    <b-list-group-item>KEGG</b-list-group-item>
                    <b-list-group-item>Gene Ontology</b-list-group-item>
                  </b-list-group>
                </b-col>
              </b-row>
            </b-card>
            <b-card class="mb-5">
              <b-card-title>常用工具</b-card-title>
              <b-row>
                <b-col
                  md="4"
                  class="mb-3"
                  v-for="tool in tools"
                  :key="tool.name"
                >
                  <b-card
                    :title="tool.name"
                    tag="article"
                    style="max-width: 20rem;"
                    class="mb-2"
                  >
                    <b-card-text style="padding: 0 20px">{{ tool.description }}</b-card-text>
                    <b-button
                      :href="tool.link"
                      target="_blank"
                      variant="primary"
                      >访问</b-button
                    >
                  </b-card>
                </b-col>
              </b-row>
            </b-card>
          </b-col>
        </b-row>
      </b-container>
    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tools: [
        {
          name: "BLAST",
          description:
            "基于序列相似性的搜索工具，用于找到数据库中与给定序列相似的序列。",
          link: "#",
        },
        {
          name: "Clustal Omega",
          description:
            "用于进行多序列比对的工具，可以比较多个蛋白质或核acid序列的相似性和差异。",
          link: "#",
        },
        {
          name: "Swiss-Model",
          description:
            "一个基于同源建模的三维蛋白质结构预测工具，可以根据已知的蛋白质结构预测未知蛋白质的结构。",
          link: "#",
        },
        {
          name: "STRING",
          description:
            "一个功能丰富的蛋白质-蛋白质相互作用数据库，为研究者提供了大量的蛋白质之间的关系数据。",
          link: "#",
        },
        {
          name: "Cytoscape",
          description:
            "一个开源的软件平台，用于可视化复杂的网络和整合这些网络与任何类型的属性数据。",
          link: "#",
        },
      ],
    };
  },
};
</script>

<style scoped>
.bioinfo-db-overview {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
</style>
